<script>
export default {
  onLaunch() {

    // #ifdef APP-PLUS
    uni.setStorageSync("token", "c0fa034d469f42608ebcf897a08eda48");
    // #endif

    uni.getPushClientId({
      success: (resPush) => {
        console.log(resPush.cid, "getPushClientId");
        this.$kt.request.request({
          api: this.$api.business.push.bind,
          data: {
            cid: resPush.cid
          },
          stateSuccess: (res1) => {
            console.log(res1, "bind");
          },
        })
      },
    });

    uni.$on("loginStatus", (res) => {
      this.getBalanceSelfMap();
      uni.getPushClientId({
        success: (resPush) => {
          console.log(resPush.cid, "getPushClientId");
          this.$kt.request.request({
            api: this.$api.business.push.bind,
            data: {
              cid: resPush.cid
            },
            stateSuccess: (res1) => {
              console.log(res1, "bind");
            },
          })
        },
      });
    });

    uni.onPushMessage((res) => {
      console.log(res, "onPushMessage");
      var json = res.data.payload;
      uni.$emit(json.emit, json.data);
      if (json.emit == "balanceChange") {
        uni.showToast({
          title: res.data.content,
          icon: 'none'
        });
      }

    });

    uni.$on("postAudit", (res) => {
      console.log("postAudit---", res);

      setTimeout(() => {
        uni.$emit("postChange:postId:" + res.id, res);
      }, 1000);

      this.getOvoUserSelf();
      if (res.auditStatusCode == "pass") {
        uni.showToast({
          title: '你的帖子审核通过了',
          icon: 'none'
        })
        return false;
      }
      if (res.auditStatusCode == "reject") {
        uni.showToast({
          title: '你的帖子审核未通过:'+res.auditRejectReason,
          icon: 'none'
        })
        return false;
      }


    });


    uni.$on("newChat", (data) => {
      console.log("newChat", data);
      uni.$emit("newChat:roomId:" + data.roomId, data);
      var self = this.$ovo.getSelf();
      if (data.userIdOfSend != self.userId) {
        // 获取未读消息数量
        var unreadMessageCount = this.$ovo.getUnreadMessageCount();
        unreadMessageCount++;

        // 设置未读消息数量
        this.$ovo.addUnreadMessage(data);

        // this.$ovo.setUnreadMessageCount(unreadMessageCount);
        // console.log("unreadMessageCount", unreadMessageCount);
      }
    });

    uni.$on("addFollowers", (data) => {
      var self = this.$ovo.getSelf();
      self.followersCount++;
      this.$ovo.setSelf(self);
    });

    uni.$on("reduceFollowers", (data) => {
      var self = this.$ovo.getSelf();
      self.followersCount--;
      this.$ovo.setSelf(self);
    })

    uni.$on("balanceChange", (data) => {
      this.$system.setBalanceMap(data);
    })




    this.getAppFront();

    this.initSexualOrientationMap();

    this.initSadomasochismAttrMap();

    this.initEmotionalOrientation();

    this.initGMTTime();

    this.getBalanceSelfMap();


  },
  onShow: function () { },
  onHide: function () { },
  methods: {
    async getOvoUserSelf() {
      await new Promise((resolve, reject) => {
        this.$kt.request.request({
          api: this.$api.business.ovo.userBind.getSelf,
          stateSuccess: (res1) => {
            this.$ovo.setSelf(res1.data);
            resolve("");
          },
        });
      });
    },

    async getAppFront() {
      await new Promise((resolve, reject) => {
        this.$kt.request.request({
          api: this.$api.system.dictI18n.getAppFront,
          stateSuccess: (res) => {
            uni.$emit("getDictOfAppFront", res.data);
            this.$kt.i18n.setAppFront(res.data);
            resolve("");
          },
        });
      });
    },
    // 初始化性取向字典
    async initSexualOrientationMap() {
      await new Promise((resolve, reject) => {
        this.$kt.request.request({
          api: this.$api.business.ovo.sexualOrientation.getMap,
          stateSuccess: (res) => {
            this.$ovo.setSexualOrientationMap(res.data);
            resolve("");
          },
        });
      });
    },
    // 初始化SM属性字典
    async initSadomasochismAttrMap() {
      await new Promise((resolve, reject) => {
        this.$kt.request.request({
          api: this.$api.business.ovo.sadomasochismAttr.getMap,
          stateSuccess: (res) => {
            this.$ovo.setSadomasochismAttrMap(res.data);
            resolve("");
          },
        });
      });
    },
    // 获取情感取向字典
    async initEmotionalOrientation() {
      await new Promise((resolve, reject) => {
        this.$kt.request.request({
          api: this.$api.business.ovo.emotionalOrientation.getMap,
          stateSuccess: (res) => {
            this.$ovo.setEmotionalOrientationMap(res.data);
            resolve("");
          },
        });
      });
    },

    // 获取当前时间戳
    async initGMTTime() {
      // system/util/getGMTTime
      await new Promise((resolve, reject) => {
        this.$kt.request.request({
          api: this.$api.system.util.getGMTTime,
          stateSuccess: (res) => {
            this.$system.setGMTTime(res.data);
            resolve("");
          },
        });
      });
    },
    getBalanceSelfMap() {
      this.$kt.request.request({
        api: this.$api.system.balance.getSelfMap,
        isNoInterceptor: true,
        stateSuccess: (res) => {
          this.$system.setBalanceMap(res.data);
        },
      });
    },
  },
};
</script>

<style>
/*每个页面公共css */
</style>
